.navbar {
    display: grid;
    grid-template-columns: 2fr 1fr;
    height: 4em;
    border-bottom: 2px solid black;
    margin-bottom: 1em;
    -ms-flex-pack:space-evenly;
        justify-content:space-evenly;
    border-bottom:var(--shadow);

}

.navbar-banner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
    margin-left:1.5rem;
    height:3em;
    padding:5px;
    font-family:var(--banner);
}

.navbar-links {
    display: grid;
    grid-column: 2/3;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 1em;
    padding:5px;
    margin-right:1rem;
}

.links-right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
    text-decoration: none;
    height:3em;
}

.links-right:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: var(--light);
    visibility: hidden;
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
        transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    -o-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.links-right:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
    -ms-transform: scaleX(1);
        transform: scaleX(1);
}


