:root {
    --dark: gray;
    --light: lightgrey;
    --primary: #eee;
    --shadow: 0 1px 5px rgba(207, 207, 207, 0.8);
    --bold: 'Rubik', serif;
    --script: 'Dancing Script', cursive;
}

a
/* Body of Page, Element Tags */

.page-wrapper {
    width: 103%;
    margin-left: -2%;
}

a {
    color: inherit;
}

body {
    font-family: 'Montserrat', sans-serif;
    background: black;
    color: white;
}

.container {
    max-width: 80%;
    margin: auto;
    padding: 1rem;
}

.wrapper {
    display: grid;
    grid-template-columns: 1fr;
    -ms-flex-align: center;
        align-items: center;
}

.grid-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    -ms-flex-align: center;
        align-items: center;
    grid-gap: 1em;
}

.wrapper>div {
    -ms-flex-align: center;
        align-items: center;
}

.grid-wrapper>div {
    padding: 1em;
    -ms-flex-align: center;
        align-items: center;
}

.center {
    text-align: center;
}

.small-caps {
    font-variant: small-caps;
}

.cursive {
    font-family: var(--script);
}

.container>h1 {
    padding-top: 1rem;
}

.link {
    text-decoration: none;
}

.button {
    text-decoration: none;
    background-color: orange;
    padding: 10px;
    border-radius: 5px;
}

/* -------------------- */

/* Jumbotron */

/* -------------------- */

.jumbotron {
    display: grid;
    background-image: url("../../components/jumbotron-banner.jpg");
    background-size: cover;
    background-position: center;
    grid-template-columns: 1fr;
    grid-auto-rows: 10em;
    text-align: center;
    -webkit-box-shadow: var(--shadow);
            box-shadow: var(--shadow);
}

#main-banner {
    -ms-flex-item-align: end;
        align-self: end;
    font-family: var(--script);
    color: var(--primary);
    font-size: 3rem;
    font-weight: 100;
}

.banner-text {
    width: 45%;
    -ms-flex-item-align: start;
        align-self: start;
    justify-self: center;
    color: var(--primary);
    background-color: rgba(128, 128, 128, .7);
    padding: 3px;
    border-radius: 5px;
}

/* -------------------- */

/* Calendar */

/* -------------------- */

.calendar {
    justify-self: center;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    grid-column: 1/2;
    padding: 5px;
}

.react-calendar {
    height: 17em;
    opacity: .6;
}

.react-calendar>div {
    color: white;
}

.react-calendar__navigation__label {
    font-size: 1.5em;
    font-family: 'Montserrat', sans-serif;
}

.react-calendar__tile {
    font-size: 1rem;
}

.events {
    -ms-flex-item-align: start;
        align-self: start;
}

.event-item {
    text-align: right;
    list-style-type: none;
}

.calendar>div {
    width: 100%;
    height: 100%;
}

/* -------------------- */

/* Sections */

/* -------------------- */

.section-white {
    background-color: white;
    height: 35rem;
    color: black;
}

.section-dark {
    background-color: var(--dark);
    height: 35rem;
}

.section-black {
    background-color: black;
    height: 35rem;
}

.section-light {
    background-color: var(--light);
    min-height: 35rem;
    color: var(--dark);
}

/* -------------------- */

/* Intro Bio */

/* -------------------- */

.subheading {
    padding: 15px;
    margin-bottom: 9rem;
    color: orange;
    text-align: center;
    font-family: var(--bold);
}

.intro-bio {
    background-image: url("./background-bio.jpg");
    background-size: cover;
    background-position: center;
}

.quote {
    text-align: left;
    max-width: 35%;
    padding-left: 4rem;
}

/* -------------------- */

/* Footer */

/* -------------------- */

footer {
    background-color: black;
    margin-top: 1rem;
}

.footer-follow {
    color: white;
    grid-area: follow;
    justify-self: end;
    margin-right: 10%;
}

.icon {
    color: white;
    border: 3px solid white;
    border-radius: 50%;
    padding: 10px;
}

.fb-icon {
    grid-area: fb-icon;
    justify-self: center;
    padding: 8px 14px 9px 14px;
}

.ig-icon {
    grid-area: ig-icon;
    justify-self: center;
}

.li-icon {
    grid-area: li-icon;
    justify-self: center;
}

.footer {
    border-top: 3px solid black;
    display: grid;
    grid-template-columns: 4fr 1fr repeat(3, .4fr) 4fr;
    -ms-flex-align: center;
        align-items: center;
    grid-gap: 1em;
    grid-template-areas: ". follow fb-icon ig-icon li-icon .";
}

/* -------------------- */

/* Bio Page */

/* -------------------- */

.banner-img {
    justify-self: center;
}

.white-text {
    color: white;
    padding-bottom: 2%;
}

.small-text {
    font-size: .5em;
}

.image {
    justify-self: center;
    max-width: 80%;
    margin: 5%;
    border-radius: 5%;
    -webkit-box-shadow: var(--shadow);
            box-shadow: var(--shadow);
}

.bio-paragraph {
    font-size: .7em;
    line-height: 1.5rem;
    padding: 1.5rem;
    color:black;
}

/* -------------------- */

/* Media Page */

/* -------------------- */

#player {
    -webkit-box-shadow: var(--shadow);
            box-shadow: var(--shadow);
}

.img-gallery {
    background: white;
    -webkit-column-count: 2;
            column-count: 2;
    line-height: 0;
    -ms-flex-align: center;
        align-items: center;
    -webkit-column-gap: .1rem;
            column-gap: .1rem;
    margin-bottom: 1rem;
}

.gallery-image {
    justify-self: center;
    margin: 5%;
    -webkit-box-shadow: var(--shadow);
            box-shadow: var(--shadow);
    max-width: 100%;
}

.label {
    font-size: .7rem;
    margin-left: 2rem;
    font-family: var(--script);
    color: black;
}

.gallery-image img {
    width: 100% !important;
    height: auto !important;
}

/* -------------------- */

/* Contact Page */

/* -------------------- */

.contact-banner {
    height: 23rem;
    -webkit-box-shadow: var(--shadow);
            box-shadow: var(--shadow);
    margin-bottom: 1rem;
}

.banner-img {
    max-width: 100%;
}

.contact-page {
    height: 15rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
    -ms-flex-align: center;
        align-items: center;
    line-height: 0;
    padding-top: 1.5rem;
}

.contact-info {
    line-height: 2rem;
    grid-column: 2/3;
}

.contact-list {
    list-style-type: none;
    -ms-flex-item-align: center;
        align-self: center;
    padding: 0px !important;
    font-size: .7rem;
}

/* ---------------- */
/* Commissions Page */
/* ---------------- */

.commission-container {
    position:relative;
}

.commission-img {
    max-width: 100%;
    height: 370px;
    opacity: 1;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-box-shadow: var(--shadow);
            box-shadow: var(--shadow);
}

.commission-banner {
    display: grid;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    height: 30rem;
    background-image: url("../CommissionsPage/composing.jpg");
    background-size: cover;
    background-position: center;
    margin-bottom: 1rem;
    font-family: var(--bold);
}

.hover-box {
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.commission-container:hover .commission-img {
    opacity: 0.3;
}
.commission-container:hover .hover-box {
    opacity: 1;
}

.text {
    background-color:var(--dark);
    color: white;
    font-size: 16px;
    padding: 16px 32px;
    border-radius:2px;
}

/* ---------------- */
/* Blog Page */
/* ---------------- */

.blog-banner {
    display: grid;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    height: 30rem;
    background-image: url("../BlogPage/composition.jpg");
    background-size: cover;
    background-position: center;
    margin-bottom: 1rem;
}

.blog-post {
    width:100%;
    height: 25rem;
    border-radius: 10px;
    background-color:var(--light);
    overflow:scroll;
    color:black;
    line-height: 1.5;
}


